<template>
  <div class="login">
    <van-nav-bar title="会员登录" left-arrow @click-left="$router.go(-1)" />
    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input class="inp" maxlength="11" placeholder="请输入手机号码" type="text" v-model="tel">
        </div>
        <div class="form-item">
          <input class="inp" maxlength="5" placeholder="请输入图形验证码" type="text" v-model="imgCode">
          <img v-if="base64" :src="base64" alt="">
        </div>
        <div class="form-item">
          <input class="inp" placeholder="请输入短信验证码" type="text" v-model="code">
          <button @click="getCode">{{totalSecond === second? '获取验证码': second+'后重新发送'}}</button>
        </div>
      </div>

      <div class="login-btn" @click="login">登录</div>
    </div>
  </div>
</template>

<script>
import { getImgCode, getCode } from '@/api/user'
export default {
  name: 'LoginPage',
  data () {
    return {
      tel: '',
      imgCode: '',
      code: '',
      base64: '',
      key: '',
      totalSecond: 60,
      second: 60,
      timer: null
    }
  },
  created () {
    this.getImgCode()
  },
  destroyed () {
    // 离开页面销毁定时器
    clearInterval(this.timer)
  },
  computed: {
    // 判断输入是否正确
    validate () {
      if (!/^1[3-9]\d{9}$/.test(this.tel)) {
        this.$toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.imgCode)) {
        this.$toast('图片验证码错误')
        return false
      }
      return true
    }
  },
  methods: {

    // 获取图片验证码
    async getImgCode () {
      const { data: { base64, key } } = await getImgCode()
      this.base64 = base64
      this.key = key
    },
    // 获取短信验证码 测试环境下为246810
    async getCode () {
      if (!this.validate) return
      await getCode(this.imgCode, this.key, this.tel)
      this.$toast('已发送')
      // 实现倒计时效果
      if (!this.timer && this.second === this.totalSecond) {
        this.timer = setInterval(() => {
          this.second--
          if (this.second < 1) {
            clearInterval(this.timer)
            this.second = this.totalSecond
            this.timer = null
          }
        }, 1000)
      }
    },
    // 登录
    async login () {
      if (!this.validate) return
      if (this.code === '') {
        this.$toast('请输入短信验证码')
        return
      }
      // TODO使用路由守卫整体到登录页 登录成功后token并不能获取到 本地存储和状态传播可能有延迟
      // 所以将用户信息存入到本地的时机也交由vuex管理
      // 跳转到首页
      try {
        await this.$store.dispatch('user/login', { // 等待登录完成
          phone: this.tel,
          smsCode: this.code
        })
        this.$toast('登录成功')
        // 获取回跳地址
        const backUrl = this.$route.query.backUrl || '/'
        this.$router.push(backUrl)
      } catch (error) {
        this.$toast.fail('登录失败')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg,#ecb53c,#ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0,0,0,.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
